import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {  isInCart, quantityCount } from '../helper/functions';
import { addItem, removeItem, increase, decrease } from '../redux/cart/cartAction';
import trash from '../assets/icons/trash.svg'
const Phone = ({PhoneProduct}) => {
    const {image,title,price} = PhoneProduct;
    const state = useSelector(state => state.cartState)
    const dispatch = useDispatch()

    return (
        <div className=' border  shadow-xl rounded-xl mx-[20px] mt-9  mb-4'>
        <div>
            <img className='w-[350px] h-[200px] object-fit border-b-2 hover:scale-90 duration-200' src={image} alt="phone"/>
        </div>
        <div className='flex justify-between mx-2  items-center  mt-3  '>
            <h2 className='text-gray-600 font-serif font-bold'>{title}</h2>
            <p className='text-green-500 lg:text-xl font-serif '>{price}</p>
         
        </div>
        <div className='flex justify-between items-center  mt-3 mb-2 mx-2'>
            <Link to={`/ProductsPhone/${PhoneProduct.id}`} className='bg-blue-900 text-white px-1 py-1 font-serif rounded-md hover:text-black duration-200' href='#a'> Details</Link>
            <div  className='text-yellow-500 text-sm  '>
            <i className="fa-solid fa-star "></i>
            <i className="fa-solid fa-star"></i>
            <i className="fa-solid fa-star"></i>
            </div>
        </div>
        <div className=' flex justify-center items-center text-xl space-x-2 '>

        {quantityCount(state, PhoneProduct.id) === 1 && <button  onClick={() => dispatch(removeItem(PhoneProduct))}><img className='w-5 rounded bg-blue-600 ' src={trash} alt='trash'/></button>}
                    {quantityCount(state, PhoneProduct.id) > 1 && <button className=''  onClick={() => dispatch(decrease(PhoneProduct))}><i class="fa-solid fa-minus text-blue-600"></i></button>}
                    {quantityCount(state, PhoneProduct.id) > 0 && <span >{quantityCount(state, PhoneProduct.id)}</span>}
                    {
                        isInCart(state, PhoneProduct.id) ?
                        <button  onClick={() => dispatch(increase(PhoneProduct))}><i class="fa-solid fa-plus text-blue-600"></i></button> :
                        <a href="#_" class="relative inline-flex items-center justify-center px-1 py-2 mt-5 mb-2 overflow-hidden font-mono font-medium tracking-tighter text-white bg-blue-600 rounded-lg group">
                        <span class="absolute w-0 h-0 transition-all duration-500 ease-out bg-green-500 rounded-full group-hover:w-56 group-hover:h-56"></span>
                        <span class="absolute inset-0 w-full h-full -mt-1 rounded-lg opacity-30 bg-gradient-to-b from-transparent via-transparent to-gray-700"></span>
                        <button class="relative" onClick={() => dispatch(addItem(PhoneProduct))}>AddToCart</button>
                        </a>
                    }
        </div>
    </div>
    );
}

export default Phone;