import React,{useState} from 'react';
import { Link } from 'react-router-dom';
import shop from '../assets/icons/shop.svg'
import logo from '../logoimge/logo.jpg'
import { useSelector } from 'react-redux';

       const Navbar = () => {
            const [isMenuOpen, setIsMenuOpen] = useState(false);
           const state = useSelector(state => state.cartState);

            return (
              <div className="px-2 py-5 mx-[10px] mt-2 rounded-md sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8  ">
                <div className="relative flex items-center justify-between font-serif lg:fixed lg:w-[1200px] md:w-[1000px] px-2 py-2 rounded-md bg-gray-900  ">
            <Link  to='/header'>
           <img className='w-[50px] rounded-full hover:scale-95 duration-200' src={logo} alt="pro"/>
            </Link>
                  <ul className="flex items-center hidden space-x-8 lg:flex  ">
                    <li >
                      <Link
                        to="/header"
                        aria-label="Our product"
                        title="Our product"
                        className="font-medium tracking-wide text-blue-600 transition-colors duration-100 hover:text-white hover:border-b border-blue-600"
                      >
                 Home
                      </Link>
                    </li>
                    <li>
                      <Link
                        to="/products"
                        className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white  hover:border-b border-blue-600"
                      >
                        Products
                      </Link>
                    </li>
                    <li>
                      <Link
                      to="/blog"
                        aria-label="Product pricing"
                        title="Product pricing"
                        className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white  hover:border-b border-blue-600"
                      >
                      Blog
                      </Link>
                    </li>
                    <li>
                      <Link
                     to="/signUp"
                        className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white  hover:border-b border-blue-600"
                      >
                  SignUp
                      </Link>
                    </li>
                  </ul>
                  <ul className="flex items-center hidden space-x-8 lg:flex">

                  <Link className='relative' to='/cart'>
                  <img  className='w-9 ' src={shop} alt="shop"/>
                  <span className=' absolute bottom-6 bg-blue-600 text-white rounded-full px-1  left-7 '>{state.itemsCounter}</span>

                  </Link>

                             <Link to='#/'>
                             </Link>
                  </ul>
                  <div className="lg:hidden">
                    <button
                      aria-label="Open Menu"
                      title="Open Menu"
                      className="p-2 -mr-1 transition duration-200 rounded focus:outline-none focus:shadow-outline hover:bg-deep-purple-50 focus:bg-deep-purple-50"
                      onClick={() => setIsMenuOpen(true)}
                    >
                      <svg className="w-5 text-gray-600" viewBox="0 0 24 24">
                        <path
                          fill="currentColor"
                          d="M23,13H1c-0.6,0-1-0.4-1-1s0.4-1,1-1h22c0.6,0,1,0.4,1,1S23.6,13,23,13z"
                        />
                        <path
                          fill="currentColor"
                          d="M23,6H1C0.4,6,0,5.6,0,5s0.4-1,1-1h22c0.6,0,1,0.4,1,1S23.6,6,23,6z"
                        />
                        <path
                          fill="currentColor"
                          d="M23,20H1c-0.6,0-1-0.4-1-1s0.4-1,1-1h22c0.6,0,1,0.4,1,1S23.6,20,23,20z"
                        />
                      </svg>
                    </button>
                    {isMenuOpen && (
                      <div className="absolute top-0 left-0 w-full  ">
                        <div className="p-5 bg-gray-800 border rounded shadow-sm  ">
                          <div className="flex items-center justify-between mb-4  ">
                            <div>
                            <Link  to='/header'>
           <img className='w-[50px] rounded-full hover:scale-95 duration-200' src={logo} alt="pro"/>
            </Link>
                             
                            </div>
                            <div>
                              <button
                                aria-label="Close Menu"
                                title="Close Menu"
                                className="p-2 -mt-2 -mr-2 transition duration-200 rounded hover:bg-gray-200 focus:bg-gray-200 focus:outline-none focus:shadow-outline"
                                onClick={() => setIsMenuOpen(false)}
                              >
                                <svg className="w-5 text-gray-600" viewBox="0 0 24 24">
                                  <path
                                    fill="currentColor"
                                    d="M19.7,4.3c-0.4-0.4-1-0.4-1.4,0L12,10.6L5.7,4.3c-0.4-0.4-1-0.4-1.4,0s-0.4,1,0,1.4l6.3,6.3l-6.3,6.3 c-0.4,0.4-0.4,1,0,1.4C4.5,19.9,4.7,20,5,20s0.5-0.1,0.7-0.3l6.3-6.3l6.3,6.3c0.2,0.2,0.5,0.3,0.7,0.3s0.5-0.1,0.7-0.3 c0.4-0.4,0.4-1,0-1.4L13.4,12l6.3-6.3C20.1,5.3,20.1,4.7,19.7,4.3z"
                                  />
                                </svg>
                              </button>
                            </div>
                          </div>
                          <nav >
                            <ul className="space-y-4  ">
                              <li className='border-b-2'>
                                <Link
                                  to="/header"
                                  className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white hover:border-b border-blue-600"
                                >
                            Home
                                </Link>
                              </li>
                              <li className='border-b-2'>
                                <Link
                                  to="/products"
                                  className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white hover:border-b border-blue-600"
                                >
                            Products
                                </Link>
                              </li>
                              <li className='border-b-2'>
                                <Link
                                  to="/blog"
                                  className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white hover:border-b border-blue-600"
                                >
                          Blog
                                </Link>
                              </li>
                              <li className='border-b-2'>
                                <Link
                               to="/signUp"
                                  className="font-medium tracking-wide text-blue-600 transition-colors duration-200 hover:text-white hover:border-b border-blue-600"
                                >
                           SingUp
                                </Link>
                              </li>
                              <Link className='relative' to='/cart'>
                  <img  className='w-9 mt-5 ' src={shop} alt="shop"/>
                  <span className=' absolute bottom-6 bg-blue-600 text-white rounded-full px-1  left-7 '>{state.itemsCounter}</span>

                  </Link>
                            <div>
                           
                            </div>
                            </ul>
                          </nav>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              
              </div>
            );
          };

export default Navbar;
