import React from 'react';
import trashIcon from "../assets/icons/trash.svg";
import { useDispatch } from 'react-redux';
import { removeItem, decrease, increase } from '../redux/cart/cartAction';

const Cart = (props) => {
    
    const dispatch = useDispatch();
    const {image, title, price, quantity} = props.data;
    return (
        <div  className='mx-4 '>
                 <div className=' xl:mt-14 mt-4 md:mt-14  mx-4 rounded  flex border items-center justify-between  '>
            <img className='w-[100px] hover:scale-95 duration-200 '  src={image} alt="/" />
            <div className=' flex space-x-16' >
                <h3 className=' font-bold font-serif border-b-2 lg:text-xl border-blue-600'>{title}</h3>
                </div>
            <div>
                <p className='font-bold text-green-600 lg:text-xl font-serif border-b-2 border-blue-600'>{price}</p>
            </div>
            <div >
                {
                    quantity > 1 ? 
                    <button onClick={() => dispatch(decrease(props.data))} ><i class="fa-solid fa-minus text-blue-600"></i></button> :
                    <button onClick={() => dispatch(removeItem(props.data))} ><img className='bg-blue-600 rounded w-5 py-1 h-6' src={trashIcon} alt="trash" /></button>
                    
                }
                <span className='mx-2 font-bold  text-2xl border-b-2 text-red-600/80 border-blue-500'>{quantity}</span>
                <button className='mr-4' onClick={() => dispatch(increase(props.data))} ><i class="fa-solid fa-plus text-blue-600"></i></button>
            </div>
        </div>
        </div>
    );
}

export default Cart;


