import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { clear, checkout } from "../redux/cart/cartAction";
import Cart from './Cart';
const CartShping = () => {
    const dispatch = useDispatch()
    const state = useSelector(state => state.cartState)

    return (
        <div>
         <div >
                {state.selectedItems.map(item => <Cart key={item.id} data={item} />)}
            </div>
            {
                state.itemsCounter > 0 && <div className='lg:flex lg:flex-row md:flex-row lg:px-3   border mt-9 mb-9 h-18  mx-7 flex justify-between flex-col   items-center font-bold font-serif' >
                        <p className=' text-blue-600 border-b-2 border-black font-serif font-bold xl:font-xl '><span>Total Items:</span> {state.itemsCounter}</p>
                        <p className='text-blue-600 border-b-2 border-black '><span>Total Payments:</span> {state.total} $</p>
                        <div className='' >
                    <button className=' px-6 py-2 hover:text-white duration-300 mt-3 mb-3 bg-red-600 rounded mr-4'  onClick={() => dispatch(clear())}>Clear</button>
                   <Link to="/CHECLOUT"><button  onClick={() =>checkout()} className="px-6 py-2 hover:text-white duration-300 mt-3 mb-3 bg-red-600 rounded mr-4">CheckOut</button></Link> 
                        </div>
                    </div>
            }

            {
                state.itemsCounter === 0 && !state.checkout && <div className=' flex flex-col justify-center items-center border mx-3 mt-3 rounded' >
                        <h3 className='border-b-2 mb-4 mt-2 xl:mt-9  text-xl font-serif border-blue-500'>Want to buy?</h3>
                        <Link className='bg-red-600 mt-2 mb-3 px-3 py-2 rounded hover:text-white duration-300' to="/products">Go to shop</Link>
                    </div>
            }

            {
                state.checkout && <div>
                        <h3>Checked out successfully</h3>
                        <Link to="/products">Buy More</Link>
                    </div>
            }
        </div>
    );
}

export default CartShping;
