import React from 'react';
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import {  isInCart, quantityCount } from '../helper/functions';
import { addItem, removeItem, increase, decrease } from '../redux/cart/cartAction';
import trash from '../assets/icons/trash.svg'
import HeadPhoneState from '../DataHeadPhone/DataHeadphone'
  
const HeadPhone = () => {
    
    
    const state = useSelector(state => state.cartState)
    const dispatch = useDispatch()
    return (
        <div className="slide-container mt-12 mx-10  border  shadow-xl rounded-xl   mb-4' bg-black/10  ">
        <Slide className="bg-blue-600">
       {
        HeadPhoneState.map(product => (
            <div className=' grid justify-center '>
                <img className='w-[300px] h-[200px] lg:w-[400px] md:w-[400px] rounded mt-1 object-fit border-b-2  hover:scale-90  duration-200' src={product.image} alt="/"/>
                <div  className='flex justify-between  items-center  mx-1  mt-3'>
                    <h2 className='text-gray-600 font-serif font-bold'>{product.title}</h2>
                    <span className='text-green-500 xl:text-xl font-serif '>{product.price}</span>
                </div>
                <div className='flex justify-between items-center mx-2 mt-3 mb-2'>
            <Link to={`/ProductsHeadphone/${product.id}`} className='bg-blue-900 text-white font-serif px-1 py-1 rounded-md hover:text-black duration-200' href='#a'> Details</Link>
            <div  className='text-yellow-500 text-sm  '>
            <i className="fa-solid fa-star "></i>
            <i className="fa-solid fa-star"></i>
            <i className="fa-solid fa-star"></i>
            <i className="fa-solid fa-star"></i>
            <i className="fa-solid fa-star"></i>
            </div>
        </div>
        <div className=' flex justify-center items-center text-xl space-x-2 '>

{quantityCount(state, product.id) === 1 && <button  onClick={() => dispatch(removeItem(product))}><img className='w-5 rounded bg-blue-600 ' src={trash} alt='trash'/></button>}
            {quantityCount(state, product.id) > 1 && <button className=''  onClick={() => dispatch(decrease(product))}><i class="fa-solid fa-minus text-blue-600"></i></button>}
            {quantityCount(state, product.id) > 0 && <span className=' text-2xl'>{quantityCount(state, product.id)}</span>}
            {
                isInCart(state, product.id) ?
                <button  onClick={() => dispatch(increase(product))}><i class="fa-solid fa-plus text-blue-600"></i></button> :
                <a href="#_" class="relative inline-flex items-center justify-center px-1 py-2 mt-5 mb-2 overflow-hidden font-mono font-medium tracking-tighter text-white bg-blue-600 rounded-lg group">
                <span class="absolute w-0 h-0 transition-all duration-500 ease-out bg-green-500 rounded-full group-hover:w-56 group-hover:h-56"></span>
                <span class="absolute inset-0 w-full h-full -mt-1 rounded-lg opacity-30 bg-gradient-to-b from-transparent via-transparent to-gray-700"></span>
                <button class="relative" onClick={() => dispatch(addItem(product))}>AddToCart</button>
                </a>
            }
</div>
            </div>
        ))
       }
        </Slide>
      </div>
    );
}

export default HeadPhone;