import React from 'react';
import LaptopState from '../DATA/DataLaptop';
import  Laptop from '../ProductLaptop/Laptop';
const LaptopStore = () => {
    return (
        <div className='grid grid-cols-2 lg:grid-cols-4 gap-6'>
       
            {
                LaptopState.map(product => <Laptop
                    key={product.id}
                    LaptopState={product}
                />)
            }
        </div>
    );
}

export default LaptopStore;
