import React from 'react';
import { Link } from 'react-router-dom';
import  HeadPhoneState from '../DataHeadPhone/DataHeadphone'
const LaptopDtails = (props) => {
             const id = props.match.params.id;
            const data =  HeadPhoneState 
             const product = data[id - 1];
         
            const {image,title,price,category,description} =product
    return (
        <div className=' xl:ml-12 xl:mt-20 md:mt-20 xl:max-w-[1200px] flex  items-center  rounded-md w-full   '>
      <div>
            <img className='xl:w-[450px] md:w-[450px] w-[350px]  object-fit hover:scale-95 duration-200 ' src={image} alt="phone"/>
        </div>
        <div className=' mr-6 w-full  rounded-md xl:px-2 xl:border-2 md:border-2'>
        <div className=' text-gray-500 text-2xl font-bold font-serif'>
        <h2 className='border-b-2 w-[150px]'>{title}</h2>
        </div>
        <div >
            <p className='border-b-2 w-[50px] text-green-600  text-2xl mt-3'>{price}</p>
            <p className='border-b-2 w-[140px] text-red-500 text-1xl mt-3  ' >Category:<span className='text-blue-500 uppercase font-bold font-serif'>{category}</span></p>
        </div>
        <div>
            <p className='text-gray-500 font-serif mt-4 text-md xl:border-b-2 lg:w-[500px] md:border-b-2'>{description}</p>
            <h2 className='mt-4 border-b-2 w-[160px] text-center py-2 rounded-md font-serif text-white hover:text-gray-600 duration-200 bg-blue-500'><Link  to="/products">Back To AppStore</Link></h2>
      </div>
        </div>
      
        </div>
    );
}

export default LaptopDtails;
