import React from 'react';
import PhoneProduct from '../DATAPHONE/DataPhone';
import  Phone from '../ProductPhone/Phone';
const PhoneStore = () => {
    return (
        <div className='grid grid-cols-2 lg:grid-cols-4 gap-6'>
       
            {
                PhoneProduct.map(product => <Phone
                    key={product.id}
                    PhoneProduct={product}
                />)
            }
        </div>
    );
}

export default PhoneStore;
