import React from 'react';
import LaptopStore from '../ProductLaptop/LaptopStore';
import PhoneStore from '../ProductPhone/PhoneStore.js';
import HeadPhone from '../ProductHeadPhone/HeadPhone'

const Product = () => {
    return (
        <div className='xl:mt-20 md:mt-22 mt-4 mb-5 mx-5'>
   
        <h2 className='text-center text-gray-900 font-bold mb-3 font-serif text-xl border-b-2 uppercase '><span className='text-blue-600 font-bold uppercase '>Phone</span>Store</h2>
        <PhoneStore/>
        <h2 className='text-center text-gray-900 font-bold mb-3 font-serif text-xl border-b-2 uppercase mt-7 '><span className='text-blue-600 font-bold uppercase '>HeadPhone</span>Store</h2>
         <HeadPhone/>
        <h2 className='text-center text-gray-900 font-bold mb-3 font-serif text-xl border-b-2 uppercase mt-7'><span className='text-blue-600 font-bold uppercase '>Laptop</span>Store</h2>
        <LaptopStore/>
        </div>
    );
}

export default Product;
