// components
import Navbar from './components/Navbar.jsx';
import Header from './components/Header.jsx';
import Footer from './components/Footer';
import LaptopDtails from './ProductLaptop/LaptopDtails'
import PhoneDtails from './ProductPhone/PhoneDtails'
import HeadPhoneDtails from './ProductHeadPhone/HeadPhoneDtails'
import ShopCart from './Cart/CartShping';
import CheckOut from './Cart/CheckOut.js';
//pages
import Blog from './pages/Blog';
import SignUp from './pages/SignUp';
import Product from './pages/Product.jsx'
//redux
import { Provider } from 'react-redux';
import store from './redux/store';
// react-router-dom
import { Route, Switch,Redirect } from 'react-router-dom';


function App() {
  return (
    <Provider store={store}>
      <Navbar/>
      <Switch>
        <Route path='/ProductsPhone/:id' component={PhoneDtails} />
        <Route path='/ProductsLaptop/:id' component={LaptopDtails} />
        <Route path='/ProductsHeadphone/:id' component={HeadPhoneDtails} />
        <Route path='/header' component={Header} />
        <Route path='/cart' component={ShopCart} />
        <Route path='/blog' component={Blog} />
        <Route path='/signUp' component={SignUp} />
        <Route path='/products' component={Product} />
        <Route path="/CHECLOUT" component={CheckOut}/>
        <Redirect to='header'/>
      </Switch>
      <Footer/>
    </Provider>
  );
}

export default App;
